import React,{useState,useEffect} from 'react';

const Header = (props)=>{
  const [name,setName] = useState('');
  const [head,setHead] = useState([]);
  
  useEffect(()=>{
    const url ='https://spreadsheets.google.com/feeds/list/1EYJ9WPjqKqe26InIhdzyFKZzBKgAKRkM04w7aaFQhxk/od6/public/values?alt=json';
    fetch(url) 
    .then(res => res.json())
    .then(data=>data.feed.entry)
    .then(entry=>{
      let items = [];
      entry.forEach(e=>{
        let index = 'gsx$'+'head'
        let item = e[index]['$t'];
        if(item.length>0) items.push(item);
      });
      (props.jpn==false)?setName(items[0]):setName(items[1])
      setHead(items); 
      document.title=name;
    });
  });
  return(
    <>
    <div className="header f">
	   <h1>{name}</h1>
     <br/>
      <div>
      	<b>
        <a href="#about" onClick={()=>{props.handlePage('about')}}>{(!props.jpn)?head[2]:head[3]}</a>
      	<a href="#cv" onClick={()=>{props.handlePage('cv')}}>{!props.jpn?head[4]:head[5]}</a>
      	<a href="#projects" onClick={()=>{props.handlePage('projects')}}>{!props.jpn?head[6]:head[7]}</a>
        <a href="#skills" onClick={()=>{props.handlePage('skills')}}>{!props.jpn?head[8]:head[9]}</a>
      	</b>
      </div>
      <br/>
      <div id="lang">
      <a onClick={()=>{props.handleJPN(false)}}>English</a><a onClick={()=>{props.handleJPN(true)}}>日本語</a>
      </div>
      <br/>
    </div>
    </>
  )
}

export default Header;